import React, { useState } from 'react'
import Scrollspy from 'react-scrollspy'
import { StyledBurger, StyledMenu } from './styles'

const Burger = ({open, setOpen}) => {
  return (
    <StyledBurger open={open} onClick={() => setOpen(!open)}>
      <div />
      <div />
      <div />
    </StyledBurger>
  )
}

const Menu = ({ open, setOpen }) => {
  return (
    <StyledMenu open={open}>
      <Scrollspy items={ ['section-about', 'section-projects', 'section-contact'] } 
                 currentClassName="active"
                 componentTag="nav"
                 style={{
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'center',
                 }}>
          <a href="#section-about" onClick={() => setOpen(false)}>
            About Me
          </a>
          <a href="#section-projects" onClick={() => setOpen(false)}>
          Projects
          </a>
        <a href="#section-contact" onClick={() => setOpen(false)}>
          Contact
          </a>
      </Scrollspy>
    </StyledMenu>
  )
}

export default function Navigation() {
  const [open, setOpen] = useState(false);
  return (
    <div>
          <Burger open={open} setOpen={setOpen}/>
          <Menu open={open} setOpen={setOpen}/>
    </div>
  )
}
